import React, { Fragment } from 'react';
import { Link } from 'react-router-dom';
import { Icon } from 'antd';
// for now these are in-common between nodes
import NodeCardHeaderFull from '../../../components/elements/node/NodeCardHeaderFull';
import NodeCardHeader from '../../../components/elements/node/NodeCardHeader';

const Audio = (props) => {
  const nodeCard = () => {
    return (
      <li className='nodelist-item'>
        <NodeCardHeader node={props.node} />
        <Link to={`/associations/${props.node.uuid}`} onClick={(e) => props.handleClick()}>
          {/*          <Icon
            type={'sound'}
            theme='outlined'
            className='node-card-icon'
            style={{ height: '11rem', paddingTop: '3rem' }}
          /> */}
          <div className='nodelist-item-audio'></div>
          <audio
            style={{
              textAlign: 'center',
              backgroundColor: 'white',
              width: '100%',
              borderRadius: '0',
            }}
            src={props.node.preview}
            controls
            type='audio/wav'
          ></audio>
        </Link>
      </li>
    );
  };

  // how the node will appear in collections
  const collectionPreview = () => {
    return (
      <Fragment>
        <Icon type={'sound'} theme='outlined' className='node-card-icon' />
      </Fragment>
    );
  };

  const fullNode = () => {
    return (
      <div className='full-node-item'>
        <NodeCardHeaderFull />
        <audio
          style={{
            textAlign: 'center',
            backgroundColor: 'white',
            width: '100%',
            borderRadius: '0',
          }}
          src={props.node.preview}
          controls
          type='audio/wav'
        ></audio>
        {/*         
          <a
          href={props.node.preview}
          target='_blank'
          rel='noopener noreferrer'
          style={{ width: '100%' }}
        >
          <Icon
            type={'sound'}
            theme='outlined'
            style={{
              fontSize: '5rem',
              color: '#b8b8b8',
              display: 'block',
              textAlign: 'center',
              padding: '3rem',
            }}
          />
        </a>*/}
      </div>
    );
  };

  const associationLink = () => {
    return (
      <Link
        to={`/associations/${props.node.uuid}`}
        onClick={(e) => this.handleLinkClick()}
        // target='_blank'
      >
        {props.node.name}
      </Link>
    );
  };

  // render the requested element
  const renderNode = () => {
    switch (props.element) {
      case 'card':
        return <Fragment>{nodeCard()}</Fragment>;
      case 'preview':
        return <Fragment>{collectionPreview()}</Fragment>;
      case 'full':
        return <Fragment>{fullNode()}</Fragment>;
      case 'association-link':
        return <Fragment>{associationLink()}</Fragment>;
      default:
        return;
    }
  };

  return <Fragment>{renderNode()}</Fragment>;
};

export { Audio };
